.loader{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 7px dashed teal;
    animation: rotate 3s infinite linear;
}

@keyframes rotate {
    from{
        transform: rotate(0deg) scale(0.5);
    }
    to{
        transform: rotate(360deg) scale(1.4);
    }
}